#Project {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  background-color: #1f1c2a;
  padding-bottom: 20px;

}

.Project-title > h3 {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 24px;
}
.Project-title > p {
  text-align: center;
  font-size: 1.2rem;
  margin-top: 10px;
  color: white;
}
.Tabs { 
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.TabsList {
  display: flex;
  font-size: 16px;
  border-radius: 12px;
  font-weight: 500;
  margin: 22px 0px;
  cursor: pointer;
  outline: none;
}
.Tab {
  padding: 0.7rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border: 1px solid;
  color: #854ce6;
  font-size: 16px;
  border-radius: 10px;
  font-weight: 500;
  /* font-size: 20px; */
  margin: 10px;
  opacity: 0.8;
  
}
.active {
  background-color: #854ce6;
   color: black;
  }

.map {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
  
}
.Table:hover{
  transform: translateY(-5px);
  box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.2);
  transition: 0.5s ease-in-out;
}
.Table > h4 {
  padding: 5px;
  text-align: left;
}
.Table > h5 {
  margin-top: 10px;
  font-size: 16px;
  text-align: center;
}
.Table > p {
  padding-top: 10px;
  padding-bottom: 30px;
  font-size: 13px;
}

.Table {
  display: flex;
  flex-direction: column;
  width: 300px;
  background-color: white;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 20px;
 

  border: 0.1px solid #306ee8;
}

@media only screen and (min-width: 300px) and (max-width: 900px) {
  #Project{
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
    padding-top: 15px;
    padding-bottom: 0;
  }
  .TabsList {
    flex-wrap: wrap;
    justify-content: center;
  }
  .Tab {
    display: flex;
    margin: 10px;
    font-size: 18px;
  }
  .map{
    padding-top: 0;
  }
}
@media only screen and (min-width: 901px) and (max-width: 1200px) {
  .TabsList {
    flex-wrap: wrap;
    justify-content: center;
  }
  .Tab {
    display: flex;
    margin: 10px;
    font-size: 18px;
  }
}
@media only screen and (min-width: 1201px) and (max-width: 1485px) {
  .TabsList {
    flex-wrap: wrap;
    justify-content: center;
  }
  .map {
    margin-left: 70px;
  }

  .Tab {
    display: flex;
    margin: 5px;
    font-size: 18px;
  }
}
.app-img {
  width: 100%;
  height: 120px;
}

.button1 {
  width: 100%;
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  color: #f2f3f4;
  padding: 12px 16px;
  border-radius: 8px;
  background-color: #854ce6;
  background-color: #1c1e27;
  color: #b1b2b3;
  cursor: pointer;
  -webkit-text-decoration: none;
  text-decoration: none;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.button1:hover {
  background-color: #854ce6;
  color: white;
}
