.Hero {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #191924;
  color: white;
  padding-bottom: 4rem;
  z-index: 1;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 70% 95%, 0 100%);
  overflow: hidden;
}

.Hero-container {
  position: relative;
  width: 100%;
  max-width: 1100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 70px;
}

.Hero-left-container {
  width: 50%;
  margin-top: 80px;
}
.Hero-right-container {
  width: 50%;
}
.Hero-right-container > img {
  width: 100%;
  height: 100%;
  max-width: 400px;
  max-height: 400px;
  border-radius: 50%;
  border: 2px solid #854ce6;
  margin: 0 auto;
}
.title {
  font-weight: 700;
  font-size: 20px;
  color: #f2f3f4;
  line-height: 58px;
}
.sub-title {
  font-size: 20px;
  line-height: 32px;
  margin-bottom: 42px;
  color: #f2f3f4;
}
@media (max-width: 1270px) {
  .Hero-container {
    max-width: 900px;
    gap: 10px;
  }
}

@media (max-width: 960px) {
  .Hero-container {
    flex-direction: column;
    gap: 0;
    width: 100%;
    padding-bottom: 30px;
    position: relative;
  }
  .Hero-left-container {
    order: 1; 
    margin-top: 50px; 
    text-align: center;
    position: relative;
    z-index: 1; 
    width: 80%;
  }
  .Hero-right-container {
    order: 2; 
    text-align: center;
  }
  .Hero-right-container > .animation {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0; 
  }

  .button-Resume {
    margin: 0 auto;
  }
}


  @media (max-width: 760px) {
    .Hero{
      clip-path: polygon(0 0, 100% 0, 100% 81%, 245% 96%, 0 100%);
      padding-bottom: 0px;
    }
    .Hero-container {
      flex-direction: column;
      gap: 0;
      width: 100%;
      padding-bottom: 50px;
    }
    .Hero-left-container {
      width: 80%;
      margin-top: 50px;
      order: 2;
      text-align: center;
      position: relative;
      z-index: 1; /* Ensure the title is above the background animation */
    }
    .Hero-right-container {
      order: 1;
      text-align: center;
    }
    .Hero-right-container > .animation {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 0; /* Ensure the animation is behind the title */
    }
  
  }

  @media (max-width: 380px) {
    .Hero-right-container > img {
      order: 1;
      top: 10px;
      right: 85px;
      max-width: 150px;
      max-height: 150px;
    }
  }

.button-Resume {
  -webkit-appearance: button;
  -moz-appearance: button;
  -webkit-appearance: button;
  -moz-appearance: button;
  appearance: button;
  -webkit-text-decoration: none;
  text-decoration: none;
  width: 95%;
  max-width: 300px;
  text-align: center;
  padding: 16px 0;
  color: #ffffff;
  border-radius: 20px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 600;
  -webkit-transition: all 0.2s ease-in-out !important;
  transition: all 0.2s ease-in-out !important;
  background: hsla(271, 100%, 50%, 1);
  background: linear-gradient(
    225deg,
    hsla(271, 100%, 50%, 1) 0%,
    hsla(294, 100%, 50%, 1) 100%
  );
  background: -moz-linear-gradient(
    225deg,
    hsla(271, 100%, 50%, 1) 0%,
    hsla(294, 100%, 50%, 1) 100%
  );
  background: -webkit-linear-gradient(
    225deg,
    hsla(271, 100%, 50%, 1) 0%,
    hsla(294, 100%, 50%, 1) 100%
  );
  box-shadow: 20px 20px 60px #1f2634, -20px -20px 60px #1f2634;
}
