/* Modal Container */
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease-in-out;
}

/* Modal Content */
.modal-content {
  background-color: #fff;
  max-width: 400px;
  border-radius: 4px;
  padding: 20px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

/* Modal Header */
.modal-header {
  padding: 10px 0;
  border-bottom: 1px solid #ccc;
}

.modal-header h2 {
  margin: 0;
  font-size: 24px;
}

/* Modal Body */
.modal-body {
  padding: 20px 0;
}

/* Modal Footer */
.modal-footer {
  padding: 10px 0;
}

/* Close Button */
.close-button {
  padding: 10px 20px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.close-button:hover {
  background-color: #0056b3;
}

/* Show modal when isOpen is true */
.modal.open {
  opacity: 1;
  pointer-events: auto;
}

.modal.open .modal-content {
  opacity: 1;
  transform: translateY(0);
}

/* Hidden by default */
.hidden {
  display: none;
}
