.container-skills-section {
  display: flex;
  justify-content: center;
  background-color: #1c1c27;
  padding-bottom: 30px;
}
.SkillsContainer {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}
.wrapper {
  width: 80%;
  padding: 20px;
  box-sizing: border-box;
}

.title-skill {
  display: flex;
  justify-content: center;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;

  color: white;
}

.Descc {

  font-size: 16px;
  color: white;
  margin-bottom: 20px;
  text-align: center;
}

.Skill {
  border: 0.1px solid #854ce6;
  padding: 10px;
  border-radius: 8px;
  width: 32%;
  background-color: #171721;
}

.SkillTitle {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
  color: white;
  text-align: center;
}

.SkillList {
  display: flex;
  justify-content:center;
  align-items: center;

  width: 100%;
  flex-wrap: wrap;
  gap: 20px;
}

.SkillItem {
  max-width: 100%;
  font-size: 16px;
  font-weight: 400;
  color: #f2f3f480;
  border: 1px solid #f2f3f480;
  border-radius: 12px;
  padding: 12px 16px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  gap: 8px;
}

.SkillImage img {
  width: 30px;
  height: 30px;
  margin-right: 10px;
}

.SkillImage,
p {
  display: flex;
  align-items: center;
}

.container-skills {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 960px) {
  .Skill {
    width: 90%;
  }
  .SkillItem {
    padding: 23px;
  }
}
@media (max-width: 760px) {
  .container-skills-section{
    padding-bottom: 10px;
  }
  .Skill {
    width: 100%;
  }
  .SkillItem {
    padding: 6px;
  }
}
