/* Experiences.css */
#Experience {
  background-image: linear-gradient(#1f1c2a, #1f1c2a 50%, #1f1c2a, #321a41);
  padding: 10px 0;
}

.experience-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.experience-card {
  display: flex;
  flex-direction: column;
  width: 300px;
  background-color: #1c1c27;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 20px;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  border: 0.1px solid #306ee8;
}

.experience-card img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  margin-bottom: 20px;
}

.experience-details > h3 {
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
  color: #f2f3f499;
  margin-bottom: 10px;
}

.experience-details > .date {
  font-size: 0.9rem;
  font-weight: 400;
  color: #777;
  margin-bottom: 5px;
}

.experience-details > .company-name {
  font-size: 0.9rem;
  font-weight: 600;
  color: #b1b2b399;
  margin-bottom: 15px;
}

.experience-details > .company-description {
  font-size: 1rem;
  color: #666;
  line-height: 1.4;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 15px;
  color: #b1b2b399;
}

.skills-list li {
  font-size: 0.9rem;
  font-weight: 500;
  color: #b1b2b399;
  margin-right: 2px;
}

.exp-header {
  text-align: center;
  color: white;
  font-size: 1.8rem;
  font-weight: 500;
  margin-bottom: 20px;
}

.exp-sub-title {
  text-align: center;
  font-size: 1.2rem;
  margin-top: 10px;
}

.experience-card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.2);
}
@media (max-width: 760px) {
  .experience-container{
    padding-top: 0;
  }
}